// Simple vowel and consonant sounds
const simplePhonemes: Record<string, string> = {
  a: "æ",
  e: "ɛ",
  i: "ɪ",
  o: "ɒ",
  u: "ʌ",
  th: "θ",
  sh: "ʃ",
  ch: "tʃ",
  ph: "f",
};

// Enhanced phoneme patterns for better word-to-IPA conversion
const phonemePatterns: Record<string, string> = {
  // Vowels
  a: "æ",
  e: "ɛ",
  i: "ɪ",
  o: "ɒ",
  u: "ʌ",

  // Common vowel combinations
  ee: "iː",
  ea: "iː",
  oo: "uː",
  ou: "aʊ",
  ow: "aʊ",
  ai: "eɪ",
  ay: "eɪ",
  ie: "aɪ",
  igh: "aɪ",
  oa: "oʊ",
  oe: "oʊ",
  oi: "ɔɪ",
  oy: "ɔɪ",

  // Consonants
  th: "θ",
  sh: "ʃ",
  ch: "tʃ",
  ph: "f",
  wh: "w",
  ng: "ŋ",
  gh: "", // Often silent

  // Common word endings
  ing: "ɪŋ",
  ed: "d",
  es: "z",
  er: "ər",
  or: "ər",
  ar: "ər",
  tion: "ʃən",
  sion: "ʒən",
};

class SoundTube {
  private enabled = false;
  private toggleButton: HTMLElement | null = null;
  private subtitleContainer: HTMLElement | null = null;
  private captionWindow: HTMLElement | null = null;
  private currentVideoId: string | null = null;
  private transcriptData: any[] = [];
  private currentSubtitleIndex = -1;
  private checkVideoInterval: number | null = null;
  private buttonAddAttempts = 0;
  private maxButtonAddAttempts = 10;
  private subtitleCheckInterval: number | null = null;

  constructor() {
    console.log("SoundTube: Starting...");
    this.createStyles();
    this.setupVideoChangeDetection();
    this.tryAddingButtonToPlayer();
  }

  private createStyles() {
    const style = document.createElement("style");
    style.textContent = `
      .ytp-button.soundtube-button {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        opacity: 0.9;
        transition: opacity 0.1s ease-in-out;
        margin: 0;
        padding: 0;
        height: 100%;
        cursor: pointer;
        font-family: 'YouTube Noto', Roboto, Arial, sans-serif;
        font-size: 16px;
        font-weight: bold;
        color: white;
      }
      
      .ytp-button.soundtube-button:hover {
        opacity: 1;
      }
      
      .ytp-button.soundtube-button.active {
        color: #ff0000;
      }
      
      .soundtube-caption-container {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        pointer-events: none;
        z-index: 41;
      }
      
      .soundtube-caption-window {
        position: absolute;
        text-align: center;
        left: 50%;
        width: auto;
        min-width: 240px;
        max-width: 80%;
        transform: translateX(-50%);
        bottom: 2%;
        margin-bottom: 61px;
        transition: margin-bottom .25s cubic-bezier(0,0,.2,1);
        z-index: 41;
      }
      
      .soundtube-caption-text {
        overflow-wrap: normal;
        display: block;
      }
      
      .soundtube-caption-line {
        display: block;
      }
      
      .soundtube-caption-segment {
        display: inline-block;
        white-space: pre-wrap;
        background: rgba(8, 8, 8, 0.75);
        font-size: 25px;
        color: #ffeb3b;
        font-family: 'YouTube Noto', Roboto, Arial, sans-serif;
        padding: 4px 8px;
        border-radius: 2px;
      }
      
      .soundtube-caption-segment.loading {
        background: rgba(8, 8, 8, 0.5);
        color: #ffffff;
        font-style: italic;
      }
      
      .soundtube-caption-segment.error {
        background: rgba(255, 0, 0, 0.5);
        color: #ffffff;
      }
      
      @keyframes fadeIn {
        from { opacity: 0; }
        to { opacity: 1; }
      }
      
      .soundtube-caption-segment {
        animation: fadeIn 0.2s ease-in-out;
      }
    `;
    document.head.appendChild(style);
  }

  private createSubtitleContainer() {
    // Remove any existing container
    const existingContainer = document.querySelector(
      ".soundtube-caption-container"
    );
    if (existingContainer) {
      existingContainer.remove();
    }

    // Find the YouTube player container
    const playerContainer = document.querySelector(".html5-video-player");
    if (!playerContainer) {
      console.log("SoundTube: Could not find video player container");
      return;
    }

    // Create container structure matching YouTube's caption structure
    this.subtitleContainer = document.createElement("div");
    this.subtitleContainer.className = "soundtube-caption-container";
    this.subtitleContainer.style.display = "none";

    this.captionWindow = document.createElement("div");
    this.captionWindow.className = "soundtube-caption-window";

    const captionText = document.createElement("span");
    captionText.className = "soundtube-caption-text";

    this.captionWindow.appendChild(captionText);
    this.subtitleContainer.appendChild(this.captionWindow);
    playerContainer.appendChild(this.subtitleContainer);

    console.log("SoundTube: Created subtitle container");
  }

  private tryAddingButtonToPlayer() {
    // Try to find the YouTube player right controls
    const rightControls = document.querySelector(".ytp-right-controls");

    if (rightControls) {
      this.addButtonToPlayer(rightControls);
    } else {
      // If not found, retry a few times
      this.buttonAddAttempts++;
      if (this.buttonAddAttempts < this.maxButtonAddAttempts) {
        setTimeout(() => this.tryAddingButtonToPlayer(), 1000);
      }
    }
  }

  private addButtonToPlayer(rightControls: Element) {
    // Don't add if button already exists
    if (document.querySelector(".soundtube-button")) {
      return;
    }

    // Find the first button in the controls to insert before it
    const firstButton = rightControls.querySelector(".ytp-button");

    // Create our button
    this.toggleButton = document.createElement("button");
    this.toggleButton.className = "ytp-button soundtube-button";
    this.toggleButton.setAttribute("aria-label", "Toggle IPA Subtitles");
    this.toggleButton.textContent = "/æ/";

    // Add click handler
    this.toggleButton.onclick = () => this.toggleEnabled();

    // Insert button at the beginning of the controls
    if (firstButton) {
      rightControls.insertBefore(this.toggleButton, firstButton);
    } else {
      rightControls.appendChild(this.toggleButton);
    }

    console.log("SoundTube: Button added to player");
  }

  private toggleEnabled() {
    this.enabled = !this.enabled;

    if (this.toggleButton) {
      this.toggleButton.classList.toggle("active", this.enabled);
    }

    // Create subtitle container if it doesn't exist or was removed
    if (!this.subtitleContainer || !document.contains(this.subtitleContainer)) {
      this.createSubtitleContainer();
    }

    if (this.subtitleContainer) {
      this.subtitleContainer.style.display = this.enabled ? "block" : "none";
    }

    if (this.enabled) {
      this.loadSubtitles();
    } else {
      this.stopSubtitleTracking();
    }
  }

  private setupVideoChangeDetection() {
    // Check for video changes
    this.checkVideoInterval = window.setInterval(() => {
      const videoId = this.getVideoId();
      if (videoId && videoId !== this.currentVideoId) {
        console.log("SoundTube: New video detected:", videoId);
        this.currentVideoId = videoId;

        // Try to add button again in case player was reloaded
        this.buttonAddAttempts = 0;
        this.tryAddingButtonToPlayer();

        // Recreate subtitle container
        this.createSubtitleContainer();

        if (this.enabled) {
          this.loadSubtitles();
        }
      }
    }, 2000);

    // Also listen for navigation events
    window.addEventListener("yt-navigate-finish", () => {
      console.log("SoundTube: Navigation detected");
      setTimeout(() => {
        const videoId = this.getVideoId();
        if (videoId && videoId !== this.currentVideoId) {
          this.currentVideoId = videoId;

          // Try to add button again in case player was reloaded
          this.buttonAddAttempts = 0;
          this.tryAddingButtonToPlayer();

          // Recreate subtitle container
          this.createSubtitleContainer();

          if (this.enabled) {
            this.loadSubtitles();
          }
        }
      }, 1000);
    });
  }

  private getVideoId(): string | null {
    const urlParams = new URLSearchParams(window.location.search);
    return urlParams.get("v");
  }

  private async loadSubtitles() {
    try {
      // Show loading state
      this.showLoadingState();

      // Get video ID and check if subtitles are available
      const videoId = this.getVideoId();
      if (!videoId) {
        throw new Error("No video ID found");
      }

      // Try to get auto-generated subtitles first
      let subtitles = await this.getAutoSubtitles();

      // If no auto-generated subtitles, try manual subtitles
      if (!subtitles || subtitles.length === 0) {
        subtitles = await this.getManualSubtitles();
      }

      // If still no subtitles, show error
      if (!subtitles || subtitles.length === 0) {
        throw new Error("No subtitles available for this video");
      }

      this.transcriptData = subtitles;
      this.currentSubtitleIndex = -1;
      this.startSubtitleTracking();

      // Hide loading state
      this.hideLoadingState();
    } catch (error) {
      console.error("SoundTube: Error loading subtitles:", error);
      this.showError(
        error instanceof Error ? error.message : "Failed to load subtitles"
      );
      this.enabled = false;
      if (this.toggleButton) {
        this.toggleButton.classList.remove("active");
      }
    }
  }

  private showLoadingState() {
    if (this.captionWindow) {
      const loadingSpan = document.createElement("span");
      loadingSpan.className = "soundtube-caption-segment loading";
      loadingSpan.textContent = "Loading IPA subtitles...";
      this.captionWindow
        .querySelector(".soundtube-caption-text")
        ?.appendChild(loadingSpan);
    }
  }

  private hideLoadingState() {
    if (this.captionWindow) {
      const loadingSpan = this.captionWindow.querySelector(
        ".soundtube-caption-segment.loading"
      );
      if (loadingSpan) {
        loadingSpan.remove();
      }
    }
  }

  private showError(message: string) {
    if (this.captionWindow) {
      const errorSpan = document.createElement("span");
      errorSpan.className = "soundtube-caption-segment error";
      errorSpan.textContent = message;
      this.captionWindow
        .querySelector(".soundtube-caption-text")
        ?.appendChild(errorSpan);

      // Remove error message after 3 seconds
      setTimeout(() => {
        if (errorSpan.parentNode) {
          errorSpan.remove();
        }
      }, 3000);
    }
  }

  private processSubtitle(text: string): string {
    // Remove HTML tags and decode HTML entities
    const cleanText = text
      .replace(/<[^>]*>/g, "")
      .replace(/&amp;/g, "&")
      .replace(/&lt;/g, "<")
      .replace(/&gt;/g, ">")
      .replace(/&quot;/g, '"')
      .replace(/&#39;/g, "'");

    // Split into words and convert each to IPA
    return cleanText
      .split(/\s+/)
      .map((word) => {
        // Remove punctuation but keep track of it
        const punctuation = word.match(/[^\w\s]$/)?.[0] || "";
        const cleanWord = word.replace(/[^\w\s]/g, "");

        // Skip empty words
        if (!cleanWord) return word;

        // Convert to IPA and add back punctuation
        return this.convertToIPA(cleanWord) + punctuation;
      })
      .join(" ");
  }

  private startSubtitleTracking() {
    if (this.transcriptData.length === 0) return;

    // Check current time every 500ms
    const checkInterval = setInterval(() => {
      if (!this.enabled) {
        clearInterval(checkInterval);
        return;
      }

      const video = document.querySelector("video");
      if (!video) return;

      const currentTime = video.currentTime;
      this.updateSubtitle(currentTime);
    }, 500);
  }

  private stopSubtitleTracking() {
    // Clear subtitle
    if (this.subtitleContainer && this.captionWindow) {
      const captionText = this.captionWindow.querySelector(
        ".soundtube-caption-text"
      );
      if (captionText) {
        captionText.innerHTML = "";
      }
    }

    // Clear any subtitle check interval
    if (this.subtitleCheckInterval) {
      clearInterval(this.subtitleCheckInterval);
      this.subtitleCheckInterval = null;
    }

    // Turn off YouTube's CC if it was enabled by us
    const ccButton = document.querySelector(
      ".ytp-subtitles-button"
    ) as HTMLElement;
    if (ccButton && ccButton.getAttribute("aria-pressed") === "true") {
      ccButton.click();
    }
  }

  private updateSubtitle(currentTime: number) {
    if (this.transcriptData.length === 0) return;

    // Find the current subtitle
    let index = this.transcriptData.findIndex((item, i) => {
      const nextItem = this.transcriptData[i + 1];
      return (
        currentTime >= item.time && (!nextItem || currentTime < nextItem.time)
      );
    });

    // If index changed, update subtitle
    if (index !== -1 && index !== this.currentSubtitleIndex) {
      this.currentSubtitleIndex = index;
      const text = this.transcriptData[index].text;
      this.showSubtitle(text);
    }
  }

  private showSubtitle(text: string) {
    if (!this.subtitleContainer || !document.contains(this.subtitleContainer)) {
      this.createSubtitleContainer();
    }

    if (!this.subtitleContainer || !this.captionWindow) return;

    const ipaText = this.textToIPA(text);

    const captionText = this.captionWindow.querySelector(
      ".soundtube-caption-text"
    );
    if (captionText) {
      captionText.innerHTML = `
        <span class="soundtube-caption-line">
          <span class="soundtube-caption-segment">${ipaText}</span>
        </span>
      `;
    }
  }

  private showMessage(message: string) {
    if (!this.subtitleContainer || !document.contains(this.subtitleContainer)) {
      this.createSubtitleContainer();
    }

    if (!this.subtitleContainer || !this.captionWindow) return;

    const captionText = this.captionWindow.querySelector(
      ".soundtube-caption-text"
    );
    if (captionText) {
      captionText.innerHTML = `
        <span class="soundtube-caption-line">
          <span class="soundtube-caption-segment">${message}</span>
        </span>
      `;
    }

    // Clear message after 3 seconds
    setTimeout(() => {
      if (this.subtitleContainer && this.captionWindow) {
        const captionText = this.captionWindow.querySelector(
          ".soundtube-caption-text"
        );
        if (captionText && captionText.textContent?.includes(message)) {
          captionText.innerHTML = "";
        }
      }
    }, 3000);
  }

  private textToIPA(text: string): string {
    // First check if it's in our common words dictionary
    const lowerWord = text.toLowerCase();
    if (commonWords[lowerWord]) {
      return commonWords[lowerWord];
    }

    // If not found, try to convert using phoneme patterns
    let ipa = lowerWord;

    // Sort patterns by length (longest first) to handle overlapping patterns
    const patterns = Object.keys(phonemePatterns).sort(
      (a, b) => b.length - a.length
    );

    for (const pattern of patterns) {
      const regex = new RegExp(pattern, "g");
      ipa = ipa.replace(regex, phonemePatterns[pattern]);
    }

    // Handle special cases
    ipa = this.handleSpecialCases(ipa);

    return ipa;
  }

  private handleSpecialCases(ipa: string): string {
    // Handle silent e at the end of words
    ipa = ipa.replace(/e$/, "");

    // Handle doubled consonants
    ipa = ipa.replace(/([bcdfghjklmnpqrstvwxz])\1/g, "$1");

    // Add stress mark to words longer than one syllable
    const vowelMatches = ipa.match(/[æɛɪɒʌiːuːaʊeɪaɪoʊɔɪ]/g);
    if (vowelMatches && vowelMatches.length > 1) {
      ipa = "ˈ" + ipa;
    }

    return ipa;
  }

  private async getAutoSubtitles(): Promise<
    Array<{ time: number; text: string }>
  > {
    // Try to find and click the auto-generated subtitles option
    const ccButton = document.querySelector(
      ".ytp-subtitles-button"
    ) as HTMLElement;
    if (!ccButton) {
      return [];
    }

    // Enable subtitles if not already enabled
    if (ccButton.getAttribute("aria-pressed") !== "true") {
      ccButton.click();
    }

    // Wait for subtitles to appear
    await new Promise((resolve) => setTimeout(resolve, 1000));

    // Get all subtitle segments
    const segments = document.querySelectorAll(".ytp-caption-segment");
    if (!segments.length) {
      return [];
    }

    return Array.from(segments).map((segment) => ({
      time: this.getCurrentVideoTime(),
      text: segment.textContent || "",
    }));
  }

  private async getManualSubtitles(): Promise<
    Array<{ time: number; text: string }>
  > {
    // Try to find the transcript button
    const transcriptButton = document.querySelector(
      'button[aria-label="Open transcript"]'
    ) as HTMLElement;
    if (!transcriptButton) {
      return [];
    }

    // Click the transcript button
    transcriptButton.click();

    // Wait for transcript to load
    await new Promise((resolve) => setTimeout(resolve, 1000));

    // Get all transcript segments
    const segments = document.querySelectorAll(
      "ytd-transcript-segment-renderer"
    );
    if (!segments.length) {
      return [];
    }

    const subtitles = Array.from(segments).map((segment) => {
      const timeElement = segment.querySelector(".segment-timestamp");
      const textElement = segment.querySelector(".segment-text");

      const timeText = timeElement?.textContent || "0:00";
      const text = textElement?.textContent || "";

      // Convert time to seconds
      const timeParts = timeText.split(":").map(Number);
      let seconds = 0;
      if (timeParts.length === 3) {
        seconds = timeParts[0] * 3600 + timeParts[1] * 60 + timeParts[2];
      } else if (timeParts.length === 2) {
        seconds = timeParts[0] * 60 + timeParts[1];
      }

      return { time: seconds, text };
    });

    // Close the transcript panel
    const closeButton = document.querySelector(
      'button[aria-label="Close transcript"]'
    ) as HTMLElement;
    if (closeButton) {
      closeButton.click();
    }

    return subtitles;
  }

  private getCurrentVideoTime(): number {
    const video = document.querySelector("video");
    return video ? Math.floor(video.currentTime) : 0;
  }

  private convertToIPA(word: string): string {
    // First check if it's in our common words dictionary
    const lowerWord = word.toLowerCase();
    if (commonWords[lowerWord]) {
      return commonWords[lowerWord];
    }

    // If not found, try to convert using phoneme patterns
    let ipa = lowerWord;

    // Sort patterns by length (longest first) to handle overlapping patterns
    const patterns = Object.keys(phonemePatterns).sort(
      (a, b) => b.length - a.length
    );

    for (const pattern of patterns) {
      const regex = new RegExp(pattern, "g");
      ipa = ipa.replace(regex, phonemePatterns[pattern]);
    }

    // Handle special cases
    ipa = this.handleSpecialCases(ipa);

    return ipa;
  }
}

// Initialize with a delay to ensure page is loaded
setTimeout(() => {
  new SoundTube();
}, 1000);
